import { Subject } from "rxjs";
var workspaceItem = new Subject();
/**
 *
 * @param item
 * @category Workspace
 */
export function newWorkspaceItem(item) {
    workspaceItem.next(item);
}
/**
 *
 * @returns
 * @category Workspace
 */
export function getNewWorkspaceItem() {
    return workspaceItem.asObservable();
}
