import { validator } from "./validator";
export var isArray = validator(function (val) { return Array.isArray(val); }, "must be an array");
export var isBoolean = validator(function (val) { return typeof val === "boolean"; }, "must be a boolean");
export var isNumber = validator(function (val) { return typeof val === "number"; }, "must be a number");
export var isString = validator(function (val) { return typeof val === "string"; }, "must be a string");
export var isObject = validator(function (val) { return typeof val === "object" && !Array.isArray(val) && val !== null; }, "must be an object (not an array or null)");
export var isUuid = validator(function (val) {
    return /^[0-9a-f]{8}-[0-9a-f]{4}-[0-5][0-9a-f]{3}-[089ab][0-9a-f]{3}-[0-9a-f]{12}$/i.test(val) || /^[0-9a-f]{36}$/i.test(val);
}, "must be a valid UUID or a 36-character hexadecimal string");
