var connectivityChangedEventName = "openmrs:connectivity-changed";
export function dispatchConnectivityChanged(online) {
    window.dispatchEvent(new CustomEvent(connectivityChangedEventName, { detail: { online: online } }));
}
export function subscribeConnectivityChanged(cb) {
    var handler = function (ev) { return cb(ev.detail); };
    window.addEventListener(connectivityChangedEventName, handler);
    return function () {
        return window.removeEventListener(connectivityChangedEventName, handler);
    };
}
export function subscribeConnectivity(cb) {
    cb({ online: navigator.onLine });
    return subscribeConnectivityChanged(cb);
}
var notificationShownName = "openmrs:notification-shown";
var toastShownName = "openmrs:toast-shown";
