var networkRequestFailedEventName = "openmrs:network-request-failed";
export function dispatchNetworkRequestFailed(data) {
    window.dispatchEvent(new CustomEvent(networkRequestFailedEventName, { detail: data }));
}
export function subscribeNetworkRequestFailed(cb) {
    var handler = function (ev) { return cb(ev.detail); };
    window.addEventListener(networkRequestFailedEventName, handler);
    return function () {
        return window.removeEventListener(networkRequestFailedEventName, handler);
    };
}
