import { map, take } from "rxjs/operators";
import { openmrsObservableFetch } from "../openmrs-fetch";
export function toVisitTypeObject(openmrsRestForm) {
    return {
        uuid: openmrsRestForm.uuid,
        display: openmrsRestForm.display,
        name: openmrsRestForm.name,
    };
}
export function getVisitTypes() {
    return openmrsObservableFetch("/ws/rest/v1/visittype")
        .pipe(map(function (results) {
        var visitTypes = results.data.results.map(toVisitTypeObject);
        return visitTypes;
    }))
        .pipe(take(1));
}
