export function setupPaths(config) {
    window.openmrsBase = config.apiUrl;
    window.spaBase = config.spaPath;
    window.spaEnv = config.env || "production";
    window.spaVersion = process.env.BUILD_VERSION;
    window.getOpenmrsSpaBase = function () { return window.spaBase + "/"; };
}
export function setupUtils() {
    window.copyText = function (source) {
        var sel = window.getSelection();
        if (sel) {
            var r = document.createRange();
            r.selectNode(source);
            sel.removeAllRanges();
            sel.addRange(r);
            document.execCommand("copy");
            sel.removeAllRanges();
        }
    };
}
