import { map, take } from "rxjs/operators";
import { openmrsObservableFetch } from "../openmrs-fetch";
export function toLocationObject(openmrsRestForm) {
    return {
        uuid: openmrsRestForm.uuid,
        display: openmrsRestForm.display,
    };
}
export function getLocations() {
    return openmrsObservableFetch("/ws/rest/v1/location")
        .pipe(map(function (results) {
        var locations = results.data.results.map(toLocationObject);
        return locations;
    }))
        .pipe(take(1));
}
